import { Link } from 'react-router-dom'

import { useAppDispatch } from '../../redux/store'
import { productItem } from '../../redux/cart/types'
import { addProduct, minusProduct, removeProduct } from '../../redux/cart/Slice'

import { typeNames } from '../../components/PizzaItem/PizzaItem'
import { ReactComponent as PlusIcon } from '../../assets/img/plus.svg'
import { ReactComponent as MinusIcon } from '../../assets/img/minus.svg'
import { ReactComponent as CrossIcon } from '../../assets/img/CartRemoveCross.svg'

type pizzaItem = {
	id: string
	imageUrl: string
	title: string
	price: number
	size: number
	type: number
	count: number
}

const CartItem: React.FC<pizzaItem> = (pizza) => {
	const dispatch = useAppDispatch()

	const onRemoveProduct = () => {
		if (window.confirm('Удалить данный товар из корзины?')) {
			dispatch(removeProduct(pizza))
		}
	}

	const onClickPlus = () => {
		dispatch(addProduct({ id: pizza.id } as productItem))
	}
	const onClickMinus = () => {
		if (pizza.count > 1) {
			dispatch(minusProduct({ id: pizza.id, type: pizza.type, size: pizza.size } as productItem))
		}
		if (pizza.count === 1) {
			if (window.confirm('Удалить данный товар из корзины?')) {
				dispatch(
					removeProduct({
						id: pizza.id,
						price: pizza.price,
						count: pizza.count,
						size: pizza.size,
					} as productItem)
				)
			}
		}
	}

	return (
		<div className='cart__item'>
			<Link to={`../pizza/${pizza.id}`} className='cart__item-img'>
				<img className='pizza-block__image' src={pizza.imageUrl} alt='Pizza' />
			</Link>
			<div className='cart__item-info'>
				<Link to={`../pizza/${pizza.id}`}>
					<h3>{pizza.title}</h3>
				</Link>
				<p>
					{typeNames[pizza.type]}, {pizza.size} см.
				</p>
			</div>
			<div className='cart__item-count'>
				<div
					onClick={onClickMinus}
					className='button button--outline button--circle cart__item-count-minus'
				>
					<MinusIcon width={10} height={10} />
				</div>
				<b>{pizza.count}</b>
				<div
					onClick={onClickPlus}
					className='button button--outline button--circle cart__item-count-plus'
				>
					<PlusIcon width={10} height={10} />
				</div>
			</div>
			<div className='cart__item-price'>
				<b>{pizza.price * pizza.count} ₽</b>
			</div>
			<div onClick={onRemoveProduct} className='cart__item-remove'>
				<div className='button button--outline button--circle'>
					<CrossIcon />
				</div>
			</div>
		</div>
	)
}

export default CartItem
